<template>
  <div>
    <van-tabs v-model="active" :swipeable="swipeable" :animated="animated" :ellipsis="ellipsis" :lineWidth="lineWidth">
      <van-tab title=" 1">
        <div slot="title">
          <img src="../../assets/bus_my_qianbao_icon.png" class="tab-title"/>
           1
        </div>
        1
      </van-tab>
      <van-tab title=" 2"> 2</van-tab>
      <van-tab title=" 3"> 3</van-tab>
      <van-tab title=" 4"> 4</van-tab>
      <van-tab title=" 5"> 5</van-tab>
      <van-tab title=" 6"> 6</van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  name: 'VantTab',
  data () {
    return {
      active: 0,
      swipeable: true,
      animated: true,
      ellipsis: false,
      lineWidth: 20
    }
  }
}
</script>

<style scoped>
  .tab-title {
    vertical-align: middle;
  }
</style>



